import React from 'react'
import Navbar from '../Home/Navbar'
import { CartProvider, useCart } from 'react-use-cart'
import { Col, Container, Row } from 'reactstrap'
import { Link } from 'react-router-dom'


        function Cart() {
  const {
    isEmpty,
    totalUniqueItems,
    totalItems,
    cartTotal,
    items,
    updateItemQuantity,
    removeItem,
    emptyCart
  } = useCart();

 

  return (
    <div>
      <h2 className="text-center">Cart ({totalUniqueItems})
      <button className="btn btn-danger" onClick={()=>emptyCart()}>Empty All Product</button>
      </h2>

      <Container>
        <Row>
          <Col md="10" className="m-auto">
          <table className="table table-dark text-center">
            <thead>
              <tr>
                <th>Image</th>
                <th>Name</th>
                <th>Quantity</th>
                <th>Price</th>
                <th>Opration</th>
              </tr>
            </thead>
            <tbody>
              {items.map((element)=>( 
                <tr>
                  <td><img src={element.images[0]} style={{ width:"70px"}}></img> </td>
                  <td>{element.title}</td>
                  <td>{element.quantity}</td>
                  <td>{ Math.round (element.price*element.quantity)}</td>
                  <td>
                    <button onClick={()=>updateItemQuantity(element.id,element.quantity-1)} className="btn btn-warning">-</button>
                    <button onClick={()=>updateItemQuantity(element.id,element.quantity+1)} className="btn btn-success mx-2">+</button>
                    <button  className="btn btn-danger" onClick={()=> removeItem(element.id)}>&times;</button>
                  </td>

                </tr>
              ))}
            </tbody>
            <tfoot>
              <tr>
                <td>Total </td>
                <td>------------</td>
                <td>{totalItems}</td>
                <td>{ Math.round (cartTotal)}</td>
                <td>
                  <Link className="btn btn-primary" to="/shop">Continue Shoping</Link>
                </td>
              </tr>
            </tfoot>
          </table>


          </Col>
        </Row>
      </Container>
    </div>
  );
}
  

function AllCart(){
    return(
    <CartProvider>
        <Navbar/>
        <br></br>
        <br></br>
        <br></br>
        <Cart/>
        </CartProvider>
    )
}

export default AllCart