import React from 'react'
import Navbar from '../Home/Navbar'
import Banner from './Banner'
import Download from '../Home/Download'
import { CartProvider } from 'react-use-cart'

function AllDownload() {
  return (
    <CartProvider>
        <Navbar/>
        <br></br>
        <br></br>
        <br></br>
        <Banner/>
        <Download/>
    </CartProvider>
  )
}

export default AllDownload