
import React,{Component} from 'react'
import Navbar from '../Home/Navbar'
import axios from 'axios'
import { Container, Row } from 'reactstrap'

export class Allproducts extends Component {
    state= {
        persons :[]
    }
    componentDidMount(){
        axios.get('/js/Api.json')
        .then((item)=>{
            this.setState(
                {
                    persons : item.data.recipes
                }

            )

        })
    }
  render() {
    return (
      <div>
        <Navbar/>
        <br></br>
        <br></br>
        <br></br>
        <br></br>
        <br></br>
        <Container>
            <Row>
        {this.state.persons.map((element)=>
        <div className='col-md-4'mt-5>
            <img src={element.image_url} className='img-fluid' style={{height:"300px"}}></img>
            <h4>{element.title}</h4>
            <h5>{element.publisher}</h5>
        </div>

        )}


            </Row>
        </Container>

      </div>
    )
  }
}

export default Allproducts